.page {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-bottom: 40px;
}

.card__productName {
  margin-bottom: 16px;
}

.card__productNameTitle {
  margin: 0 0 4px;
  font-weight: 500;
}

.card__productNameText {
  color: #616070;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  gap: 85px;
  margin-bottom: 16px;
  display: flex;
}

.card__priceLabel {
  color: #616070;
  font-weight: bold;
}

.card__priceValue {
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: all .7s;
}

.container-stars {
  justify-content: space-between;
  align-items: center;
  gap: 17px;
  margin-bottom: 24px;
  display: flex;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars-text {
  font-size: 10px;
  line-height: 14px;
}
